import { useState } from "react";
import styled,{createGlobalStyle} from "styled-components";

const GlobalStyleComponent = createGlobalStyle`
 *{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   background: rgba(9, 25, 248, 1);
   
 }
 body{
   display: flex;
   align-items: center;
   justify-content: center;
 }
`;
const CalcContainer = styled.div`
  border: 5px solid green;
  height: 80vh;
  width: 500px;
  margin:50px auto;
  background: #fff;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */

`;
const CalcHeader = styled.div`
      height: 60px;
      width: 100%;
      background: #fff;

      & h1{
        background: #fff;
        font-size: 2rem;
        font-weight: bold;
        text-align: center;
        margin-top: 10px;
        text-decoration: underline;

      }

`;
const CalcBody = styled.div`
  background: #fff;
`;
const Button = styled.button`
 border: none;
 background: rgb(248, 45, 9);
 padding: 15px 25px;
 margin: 10px;
 font-size: 2.5rem;
 border-radius: 7px;
 color: #fff;

 :hover{
   background: rgb(184, 248, 9);
   transition: 0.3s ease-in-out;
   color: #000;
 }
 :active{
  background: rgba(184, 248, 9,0.2);
 }
`;
const InputContainer = styled.div`
 background: #fff;
 display: flex;
 align-items: center;
 justify-content: center;
 margin:0 10px;
`;
const ButtonContainer = styled.div`
 display: flex; 
 flex-direction: row;
 flex-wrap: wrap;
 width: 400px;
 text-align: center;
 justify-content: center; 
 margin: auto;
 background: #fff;
`;
const Input = styled.input`
 width: 80%;
 font-size: 2rem;
 height: 70px;
 color: #fff;
 border-radius: 10px;
 text-align: right;

:active,:focus{
  outline: none;
}
`;
function App() {
  const [result,setResult] = useState("")

  const handleClick = (e) => {
    setResult(result.concat(e.target.name))
  }

  const handleBackspace = () => {
    setResult(result.slice(0, - 1))
  }
  const handleClear = () => {
    setResult("")
  }
  const calculate = () => {
    try{
      setResult(eval(result).toString())
    }catch (err) {
      setResult("Invalid Input")
    }
  } 

  return (
    <>
      <GlobalStyleComponent/>
      <CalcContainer>
        <CalcHeader>
           <h1>SIMPLE CALCULATOR</h1> 
        </CalcHeader>
        <CalcBody>
          <InputContainer>
          <Input type='text' value={result} />
          </InputContainer>
          <ButtonContainer>
           <Button onClick={handleClear}>Clear</Button>
           <Button onClick={handleBackspace}>C</Button>
           <Button name="/" onClick={handleClick}>&divide;</Button>
           <Button name="7" onClick={handleClick}>7</Button>
           <Button name="8" onClick={handleClick}>8</Button>
           <Button name="9" onClick={handleClick}>9</Button>
           <Button name="*" onClick={handleClick}>&times;</Button>
           <Button name="4" onClick={handleClick}>4</Button>
           <Button name="5" onClick={handleClick}>5</Button>
           <Button name="6" onClick={handleClick}>6</Button>
           <Button name="-" onClick={handleClick}>&ndash;</Button>
           <Button name="1" onClick={handleClick}>1</Button>
           <Button name="2" onClick={handleClick}>2</Button>
           <Button name="3" onClick={handleClick}>3</Button>
           <Button name="+" onClick={handleClick}>+</Button>
           <Button name="0" onClick={handleClick}>0</Button>
           <Button name="." onClick={handleClick}>.</Button>
           <Button name="=" onClick={calculate}>=</Button>
           </ButtonContainer>
        </CalcBody>

      </CalcContainer>
    </>
  );
}

export default App;
